import React from 'react'

 var FooterPageCss =require('./footer.css')

 export default class FooterPage extends React.Component{
     render(){
         return(
            <div className={FooterPageCss.en}>
            <div className={FooterPageCss.fir}>
                <p>校园二手交易平台 </p>
                <p>Second-hand trading platform</p>
                <p>这是一款专门针对在校大学生出售闲置物品的网站。</p>
            </div>
            <div className={FooterPageCss.di}>
                <p>总导演：张小琳</p>
                <p>总策划：张小琳</p>
                <p>技术支持：张小琳</p>
                
            </div>
            <div className={FooterPageCss.er}>
                <p className={FooterPageCss.h9}>版权归张小琳个人所有</p>
                <p className={FooterPageCss.pp}>这个项目是由我一个人完成，靠着一次次的修改和完善，每一处的细节都是我精心设计调整，可能和线上的电子商务平台还相差很远，但我也会向着完美出发</p>
            </div>
        </div>
         )
     }
 }