import React from 'react'
import {Link } from 'react-router-dom'
import { Form, Icon, Input, Button, Checkbox, Card } from 'antd';


var LoginPageCss = require('./login.css')
class LoginPage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            username: '',
            password: ''


        }
    }
    changeValue = e => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    handleSubmit = e => {
        if (this.state.username == '123zml' && this.state.password == '12345678') {
            alert('登录成功');
            window.localStorage.setItem("username", "张小琳")
            window.localStorage.setItem("token", "12345")

            this.props.history.push({ pathname: '/secret', state: { message: "hello" } })

        } else {
            alert('登录失败');
        }
        console.log(window.localStorage.getItem("username"))
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <div>
                <div className={LoginPageCss.titl}>
                    <p>校内二手交易平台</p>
                </div>
                <div className={LoginPageCss.deng}>
                    <div className={LoginPageCss.left}>
                       <h2>登录</h2>
                            <Form onSubmit={this.handleSubmit} className={LoginPageCss.form}>
                                <Form.Item>
                                    {getFieldDecorator('username', {
                                        rules: [{ required: true, message: '用户名不能为空！' }
                                            , {
                                            min: 5, max: 10,
                                            message: '长度不在范围内'
                                        }, {
                                            pattern: new RegExp('^\\w+$', 'g'),
                                            message: '用户名必须为字母或者数字'
                                        }],
                                    })(
                                        <Input
                                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                            placeholder="用户名/手机号/邮箱"
                                            id="username" name="username"
                                            value={this.state.username}
                                            onChange={this.changeValue} type="username"
                                            className={LoginPageCss.input}

                                        />,
                                    )}
                                </Form.Item>
                                <Form.Item>
                                    {getFieldDecorator('password', {
                                        rules: [{ required: true, message: '请输入你的密码!' }],
                                    })(
                                        <Input
                                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                            type="password"
                                            placeholder="密码"
                                            id="password" name="password"
                                            value={this.state.password}
                                            onChange={this.changeValue}
                                            className={LoginPageCss.input}
                                        />,
                                    )}
                                </Form.Item>
                                <Form.Item className={LoginPageCss.ji}>
                                    {getFieldDecorator('remember', {
                                        valuePropName: 'checked',
                                        initialValue: true,
                                    })(<Checkbox>记住密码</Checkbox>)}
                                    <a className={LoginPageCss.emm} href="">
                                        忘记密码
                  </a>
                                    <Button  htmlType="submit"
                                        className={LoginPageCss.btn}
                                        onClick={this.handleSubmit}>
                                        登录
                  </Button>

                                </Form.Item>

                            </Form>
                       
                    </div>
                    <div className={LoginPageCss.right}>
                    <p>还没账号？马上注册</p>
                   <Link to='/register'><Button  
                                        className={LoginPageCss.btm}>
                                        
                                        注册
                  </Button>
                  </Link> 
                    </div>
                </div>
            </div>
        )
    }
}
export default Form.create()(LoginPage);