import React from 'react'
import { Input, Button } from 'antd';
import FooterPage from '../../Footer/footer';
const { TextArea } = Input;
var tianxiepageCss = require('./tianxie.css')
export default class tianxiepage extends React.Component {
    render() {
        return (
            <div className={tianxiepageCss.input1}>

                <Input placeholder="邮箱*" className={tianxiepageCss.input} />
                <Input placeholder="住址*" className={tianxiepageCss.input} />
                <Input placeholder="职业*" className={tianxiepageCss.input} />
                <Input placeholder="证件号*" className={tianxiepageCss.input} />
                <TextArea rows={4} placeholder="为什么申请" className={tianxiepageCss.text} />
                <div className={tianxiepageCss.but}>
                    <Button>发送给Miss Zhang</Button>
                </div>
                
                
            
            </div >
        )
    }
}