import React from 'react'
import FooterPage from '../Footer/footer';
import { Icon } from 'antd';
import {Link} from 'react-router-dom'
import '../Login/login'

var FirstCss = require('./first.css')

export default class First extends React.Component {
    render() {
        return (
            <div className={FirstCss.all}>
                <div className={FirstCss.al}>
                    <div className={FirstCss.title}>
                        <p>校内二手交易平台</p>
                    </div>
                    <div className={FirstCss.photo}>
                        <img src="image/1.png" width={250} height={400}></img>
                    </div>
                    <div className={FirstCss.photo}>
                        <img src="image/2.png" width={250} height={400}></img>
                    </div>
                    <div className={FirstCss.photo}>
                        <img src="image/3.png" width={250} height={400}></img>
                    </div>
                    <div className={FirstCss.photo}>
                        <img src="image/4.png" width={250} height={400}></img>
                    </div>
                </div>
                <div className={FirstCss.foot}>
                    <FooterPage/>
                </div>
                <div className={FirstCss.character}>
                <div className={FirstCss.fir}>
                <Icon type="sketch" style={{fontSize:'40px'}}/>
                 <Link to='/login'><h2>登录注册</h2></Link> 
                <p>更多精彩内容登录了解全面的校内正规的交易平台</p>
                </div>
                <div className={FirstCss.sec}>
                <Icon type="windows"  style={{fontSize:'40px'}} />
                <Link to="/list"><h2>网站主页</h2></Link> 
                <p>逛一逛瞧一瞧，实时更新总有自己想要的</p>
                </div>
                <div className={FirstCss.thi}>
                <Icon type="codepen"  style={{fontSize:'40px'}}/>
                <h2>忘记密码</h2>
                <p>安全的账号验证快捷的密码找回</p>
                </div>
                <div className={FirstCss.for}>
                <Icon type="home"  style={{fontSize:'40px'}}/>
                <Link to= '/join'><h2>我要入驻</h2></Link> 
                <p>欢迎加入这个大家庭，将闲置物品变废为宝吧。</p>
                </div>
                
                </div>
            </div>
        )
    }
}