import React from 'react'
import { Divider } from 'antd';
import { Icon } from 'antd';
import { Link } from 'react-router-dom'
import { BrowserRouter,Route} from 'react-router-dom'
import tianxiepage from './Tianxie/tianxie'
import FooterPage from '../Footer/footer';

var JoinPageCss=require('./join.css')

export default class JoinPage extends React.Component{
    render(){
        return(
            <div>
                <div className={JoinPageCss.head}>
                    <img src="image/3.png" width={1400} height={200}></img>
                    <p>商家入驻申请</p>
                </div>
                <div className={JoinPageCss.next}>
                <Divider>
                    <p className={JoinPageCss.next1}>
                    填写入驻表单
                    </p></Divider>
                    <p className={JoinPageCss.next2}>我们会根据您提交的一个表单来进行审批流程，请耐心等待...</p>
                    <div className={JoinPageCss.next3}>
                    <Icon type="environment" theme="filled"  style={{fontSize:'40px'}}/>
                    <p>校内二手市场</p>
                    <p>总部地址:四川   成都  金堂</p>
                    <p>总负责人:Miss Zhang
                    </p>
                    </div>
                    <div className={JoinPageCss.next3}>
                    <Icon type="phone" theme="filled" style={{fontSize:'40px'}} />
                    <p>找 BOSS</p>
                    <p>手机:12345678900</p>
                    <p>email:1099693113@qq.com</p>
                    </div>
                    <div className={JoinPageCss.next3}>
                   <Link to=''></Link> <Icon type="mail" theme="filled"  style={{fontSize:'40px'}} />
                    <p>信息</p>
                    <p>
                        我们工作人员会及时处理您提交的
                    </p>
                    <p>申请，请留意您的信息。</p>
                    </div>
                    <div className={JoinPageCss.next3}>
                    <Icon type="pushpin" theme="filled"  style={{fontSize:'40px'}} />
                    <p>注意事项</p>
                    <p>本平台仅针对本校师生，</p>
                    <p>
                     我们会进行信息审核以保证
                    </p>
                    <p>
                    用户的良好购物体验。并承诺
                    </p>
                <p>
                    <BrowserRouter>
                    <Link to="/tianxie">
                    <h9 className={JoinPageCss.h9}>加入平台</h9>
                    </Link>
                    <Route path="/tianxie" component={tianxiepage}></Route>
                    </BrowserRouter>
                    后会诚信交易。</p>
                    </div>
                </div>
                
            </div>
        )
    }
}