import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import First from './FirstPage/first'
import * as serviceWorker from './serviceWorker';
import SummerPage from './Summer/summer'
import {BrowserRouter,Route,Switch,Redirect} from 'react-router-dom'
import LoginPage from './Login/login';
import RegistrationForm from './Login/Register/register'
import HeaderPage from './Header/header'
import ListPage from './List/list'
import JoinPage from './Join/join'
import tianxiepage from './Join/Tianxie/tianxie'


export default class Root extends React.Component{
    render(){
        return(
            <div>
                 <BrowserRouter basename='vacation'>
                 <Switch>
                 <Route path='/' exact component={First}></Route>
                <Route path="/summer" component={SummerPage}></Route>
                <Route path='/login' component={LoginPage}/>
                <Route path='/register' component={RegistrationForm}/>
                <Route path='/header' component={HeaderPage}/>
                <Route path='/list' component={ListPage}/>
                <Route path='/join' component={JoinPage}></Route>
                <Route path='/tianxie' component={tianxiepage}></Route>
                </Switch>
                </BrowserRouter>
               
            </div>
        )
    }
}
  
ReactDOM.render(<Root />, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
