import React from 'react'
import {
  Form,
  Input,

  Icon,
  Cascader,
  Select,

  Checkbox,
  Button,
  AutoComplete,
  Card,
} from 'antd';
import { Upload, message } from 'antd';

const { Option } = Select;
const AutoCompleteOption = AutoComplete.Option;

const residences = [
  {
    value: '四川',
    label: '四川',
    children: [
      {
        value: '内江',
        label: '内江',

        children: [
          {
            value: '隆昌',
            label: '隆昌',
          },
        ],
      },
    ],
  },
  {
    value: '重庆',
    label: '重庆',
    children: [
      {
        value: '合川',
        label: '合川',

        children: [
          {
            value: '太和镇',
            label: '太和镇',
          },
        ],
      },
    ],
  },
  {
    value: '北京',
    label: '北京',
    children: [
      {
        value: '朝阳区',
        label: '朝阳区',

        children: [
          {
            value: '三里屯',
            label: '三里屯',
          },
        ],
      },
    ],
  },
  {
    value: '江苏',
    label: '江苏',
    children: [
      {
        value: '南京',
        label: '南京',
        children: [
          {
            value: '中华门',
            label: '中华门',
          },
        ],
      },
    ],
  },
];
function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file) {
  const isJPG = file.type === 'image/jpeg';
  if (!isJPG) {
    message.error('You can only upload JPG file!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Image must smaller than 2MB!');
  }
  return isJPG && isLt2M;
}

var RegistrationFormCss = require('./register.css');
class RegistrationForm extends React.Component {
  state = {
    loading: false,
  };

  handleChange = info => {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl =>
        this.setState({
          imageUrl,
          loading: false,
        }),
      );
    }
  };
  state = {
    confirmDirty: false,
    autoCompleteResult: [],
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  handleConfirmBlur = e => {
    const { value } = e.target;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback('Two passwords that you enter is inconsistent!');
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  handleWebsiteChange = value => {
    let autoCompleteResult;
    if (!value) {
      autoCompleteResult = [];
    } else {
      autoCompleteResult = ['.com', '.org', '.net'].map(domain => `${value}${domain}`);
    }
    this.setState({ autoCompleteResult });
  };



  render() {
    const { getFieldDecorator } = this.props.form;
    const { autoCompleteResult } = this.state;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: '86',
    })(
      <Select style={{ width: 70 }}>
        <Option value="86">+86</Option>
        <Option value="87">+87</Option>
      </Select>,
    );

    const websiteOptions = autoCompleteResult.map(website => (
      <AutoCompleteOption key={website}>{website}</AutoCompleteOption>
    ));
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? 'loading' : 'plus'} />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    const { imageUrl } = this.state;

    return (
      <div className={RegistrationFormCss.title}>
        <Card title="注册" style={{ marginTop: 10 }}>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item label="邮箱">
              {getFieldDecorator('email', {
                rules: [
                  {
                    type: 'email',
                    message: 'The input is not valid E-mail!',
                  },
                  {
                    required: true,
                    message: '请输入你的E-mail!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="密码" hasFeedback>
              {getFieldDecorator('password', {
                rules: [
                  {
                    required: true,
                    message: '请输入你的密码!',
                  },
                  {
                    validator: this.validateToNextPassword,
                  },
                ],
              })(<Input.Password />)}
            </Form.Item>
            <Form.Item label="确认密码" hasFeedback>
              {getFieldDecorator('confirm', {
                rules: [
                  {
                    required: true,
                    message: '请确认你的密码!',
                  },
                  {
                    validator: this.compareToFirstPassword,
                  },
                ],
              })(<Input.Password onBlur={this.handleConfirmBlur} />)}
            </Form.Item>

            <Form.Item label="学校省份">
              {getFieldDecorator('residence', {
                initialValue: ["内江", '隆昌', ''],
                rules: [
                  { type: 'array', required: true, message: 'Please select your habitual residence!' },
                ],
              })(<Cascader options={residences} />)}
            </Form.Item>
            <Form.Item label="电话号码">
              {getFieldDecorator('phone', {
                rules: [{
                   required: true,
                    message: '请输入你的手机号!' 
                  }
                ,{
                  min: 11, max: 11,
                  message: '电话号码格式不正确'
                },
                {
                  pattern: new RegExp('w+$', 'g'),
                  message: '学号必须为数字'
                },
              ],
              })(<Input addonBefore={prefixSelector} style={{ width: '100%' }} />)}
            </Form.Item>
            <Form.Item label="学号">
              {getFieldDecorator('number', {
                rules: [
                  {
                    min: 8, max: 8,
                    message: '学号格式不正确'
                  },
                  {
                    pattern: new RegExp('w+$', 'g'),
                    message: '学号必须为数字'
                  },
                  {
                    required: true,
                    message: '请输入你的学号!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <div className={RegistrationFormCss.dian}>
              <p>
                上传学生证首页：
        </p>
            </div>
            <div className={RegistrationFormCss.upload}>
              <Upload

                name="avatar"
                listType="picture-card"

                showUploadList={false}
                action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                beforeUpload={beforeUpload}
                onChange={this.handleChange}
              >
                {imageUrl ? <img src={imageUrl} alt="avatar" /> : uploadButton}
              </Upload>
            </div>
            <Form.Item {...tailFormItemLayout}>
              {getFieldDecorator('agreement', {
                valuePropName: 'checked',
              })(
                <Checkbox>
                  我已经阅读了<a href="http://terms.alicdn.com/legal-agreement/terms/TD/TD201609301342_19559.html?spm=a2145.7268393.0.0.f9aa5d7cUWWGU5">用户协议</a>
                </Checkbox>,
              )}
            </Form.Item>
            <Form.Item {...tailFormItemLayout}>
              <Button type="primary" htmlType="submit">
                注册
          </Button>
            </Form.Item>

          </Form>
        </Card>
      </div>
    );
  }
}
export default Form.create()(RegistrationForm);
const WrappedRegistrationForm = Form.create({ name: '注册' })(RegistrationForm);

